.Button {
  font-size: 25px;
  padding: 6px 17px;
  margin: 5px;
  width: 80px;
  background: #424242;
  border: none;
  border-radius: 10px;
  -webkit-box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  outline: none;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.Button:hover {
  cursor: pointer;
}

.Button:active {
  -webkit-box-shadow: none;
          box-shadow: none;
}


